
// Variables
// --------------------------

$fa-font-size-base:   $font-size-base;
$fa-font-path:        "//use.fontawesome.com/releases/v5.3.1/webfonts";

@import "bower_components/font-awesome/web-fonts-with-css/scss/fontawesome.scss";
@import "bower_components/font-awesome/web-fonts-with-css/scss/solid.scss";
@import "bower_components/font-awesome/web-fonts-with-css/scss/brands.scss";
@import "bower_components/font-awesome/web-fonts-with-css/scss/shims.scss";

// Mixin makes a fontawesome element on :before
// @include fa-icon-force($fa-var-chevron-right)
//
// $icon: the fontawesome variable name
// $style: the font awesome style to use 'regular' or 'brand' or 'default/solid'

@mixin fa-icon-force($icon, $style: 'default', $selector: '&:before') {
  #{$selector} {
    content: fa-content($icon);
    @extend %fa-icon;
    @if ($style == 'regular') {
      @extend .far;
    } @else if ($style == 'brand') {
      @extend .fab;
    } @else {
      @extend .fa;
    }
    @content;
  }
}